import React from 'react';
import { Card } from 'react-bootstrap';
import './ExerciseCard.css';

function ExerciseCard({exercise, blured}) {
    return (
        <div className="c-exercise-card">
            <div className="card-content">
                <div className="card-front">
                    <Card className = {blured ? "blured": ""}>
                        <Card.Img variant="top" src={exercise.image}/>
                        <Card.Body>
                            <Card.Title>{exercise.name}</Card.Title>
                        </Card.Body>
                    </Card>
                </div>
                <div className="card-back">
                    <Card className = {blured ? "blured": ""}>
                        <Card.Body>
                            <Card.Title>{exercise.name}</Card.Title>
                            {/* <Card.Text>Description</Card.Text> */}
                            <Card.Img src="https://cdn3.f-cdn.com/contestentries/255734/10692574/55b92f428c17f_thumb900.jpg"/>
                        </Card.Body>
                    </Card>
                </div>
            </div>
        </div>
    );
}

export default ExerciseCard;